@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
html {
  height: 100%; }

body {
  height: 100%;
  margin: 1% 10%;
  font-family: "Architects Daughter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
